.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    background-color: transparent;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 100;
    color: white;
}

.logo {
    font-weight: 800;
    letter-spacing: 4px;
    font-size: 1.8rem;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.links {
    display: flex;
    gap: 2rem;
}

.links a {
    font-weight: 500;
    transition: color 0.3s ease;
}

.links a:hover {
    color: var(--tesla-light);
}

.auth {
    font-weight: 500;
}

.logoutButton {
    background: none;
    border: none;
    color: white;
    font-family: inherit;
    font-size: inherit;
    font-weight: 500;
    cursor: pointer;
    transition: color 0.3s ease;
}

.logoutButton:hover {
    color: var(--tesla-light);
}

.registerButton {
    padding: 0.5rem 1rem;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 4px;
    transition: background-color 0.3s ease;
}

.registerButton:hover {
    background-color: rgba(255, 255, 255, 0.2);
    color: white !important;
}